@import url(https://fonts.googleapis.com/css?family=Open+Sans|Pacifico);
@import url(https://fonts.googleapis.com/css?family=Roboto);
html,
body {
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
}

p {
  display: inline-block;
  margin: 0;
  word-break: break-word;
}
/* Ekraan ning selle värv */
.App {
  background-color: #ffffff;
  height: 100vh;
  width: 100vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: center;
  justify-content: center;
}

/* Chatroomi iseenda mõõtmed ja asjad. */
.chatroom {
  width: inherit;
  height: inherit;
  box-shadow: 0 0 1px 0 gray;
}

/* HEADERI ASJAD ALGAVAD SIIN*/
.chatroom header {
  display: flex;
  align-items: center;
  margin: 0px;
  width: inherit;
  height: 60px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.09);
  background-color: #ffffff;
}

.chatroom header figure {
  display: flex;
  align-items: center;
  margin: 0px;
  padding-left: 28.1px;
}

.chatroom header figure img {
  height: 45px;
  width: 45px;
}

.chatroom header figure figcaption {
  float: right;
  color: #62e2c0;
  padding: 0px 12px 0px 12px;
  font-family: Roboto;
  font-style: normal;
  font-size: 15px;
  font-weight: 500;
  letter-spacing: 0.25px;
}

/* MAIN ASJAD ALGAVAD SIIN*/
.chatroom .chats {
  padding: 50px 0px 50px 0px;
  margin: 0px 0 0;
  height: -webkit-calc(100% - 218px); /* Chrome, Safari */
  overflow-y: scroll;
}

/*textboxi sisu*/
.chatroom .chats .chat {
  font-family: Roboto, regular;
  background: #ddfde8;
  position: relative;
  padding: 10px 15px 14px 15px;
  font-size: 16px;
  letter-spacing: 0.3px;
  color: #475277;
  border-radius: 2px;
  list-style: none;
  float: left;
  clear: both;
  margin-inline-end: 61.2px;
  margin-inline-start: 61.2px;
  margin-top: 1px;
  max-width: 450px;
  word-wrap: normal;
  white-space: initial;
}

.chatroom .chats .chat.right {
  background: #e7e9ff;
}

/* textboxi üleval info user/aeg */
.chatroom .chats .chat div.usernametime {
  display: flex;
  position: absolute;
  top: -16px;
  left: -0.1px;
  font-size: 12px;
}

.chatroom .chats .chat div.usernametime figcaption {
  margin-right: 5px;
  opacity: 0.96;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: 0.2px;
  color: #62e2c0;
}

.chatroom .chats .chat div.usernametime time {
  margin-right: 5px;
  color: #475277;
  opacity: 0.54;
  font-size: 10px;
}

.chatroom .chats .chat div.usernametime.right {
  flex-direction: row-reverse;
  position: absolute;
  right: -0.1px;
}

.chatroom .chats .chat div.usernametime.right figcaption {
  margin-right: 0px;
  color: #a5acff;
  opacity: 0.85;
}

/* roboti ikoon */
.chatroom .chats .chat img {
  width: 18.9px;
  height: 19.5px;
  border-radius: 50%;
  position: absolute;
  top: 10px;
  left: -25px;
}

.chatroom .chats .chat.right {
  float: right;
  clear: both;
}
/* FOOOTERI ASJAD ALGAVAD SIIN*/
.chatroom footer {
  height: 58px;
  width: inherit;
}

.chatroom footer .input .inputdiv {
  width: auto;
  border: solid 1px #62e2c0;
  border-width: 2px 0px 0px 0px;
  opacity: 0.5;
}

.chatroom footer .input .inputdiv input[type="text"] {
  padding: 17px 21px;
  font-size: 16px;
  font-family: Roboto;
  height: 21px;
  width: 80%;
  outline: none;
  border: none;
  letter-spacing: 0.3px;
}
.chatroom footer .input .inputdiv input[type="submit"] {
  background-color: transparent;
  color: #62e2c0;
  border: none;
  outline: none;
  font-size: 36px;
  font-family: "Courier New", Courier, monospace;
  position: absolute;
  right: 10px;
}

@media only screen and (max-width: 1100px) {
  .chatroom .chats .chat {
    max-width: 350px;
  }
}

@media only screen and (max-width: 900px) {
  .chatroom .chats .chat {
    max-width: 300px;
  }
}

@media only screen and (max-width: 800px) {
  .chatroom .chats .chat {
    max-width: 270px;
    margin-top: 7px;
  }
}

@media only screen and (max-width: 700px) {
  .chatroom .chats .chat {
    max-width: 230px;
    margin-top: 7px;
  }
}

@media only screen and (max-width: 560px) {
  .chatroom .chats .chat {
    max-width: 230px;
    margin-top: 18px;
  }
}

@media only screen and (max-width: 450px) {
  .chatroom .chats .chat {
    max-width: 230px;
    margin-top: 18px;
    margin-inline-end: 20px;
    margin-inline-start: 20px;
    padding: 4px 15px 7px 15px;
  }

  .chatroom .chats .chat img {
    display: none;
  }

  .chatroom .chats {
    padding: 29px 0px 29px 0px;
    margin: 0px 0 0;
    height: -webkit-calc(100% - 176px); /* Chrome, Safari */
    overflow-y: scroll;
  }
}

div.smallTriangle.left {
  position: absolute;
  margin: 0;
  top: 40%;
  left: -4.05px;
  width: 8.1px;
  height: 8.1px;
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);

  background-color: #ddfde8;
}

div.smallTriangle.right {
  position: absolute;
  margin: 0;
  top: 40%;
  right: -4.05px;
  width: 8.1px;
  height: 8.1px;
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);

  background-color: #e7e9ff;
}
